<template>
    <div ref="container" class="bg">
        <el-button v-show="!isScreenshot" class="close" @click="close"></el-button>
        <el-button v-show="!isScreenshot" class="save" @click="sendPreview1"></el-button>

        <!--      <div>-->
        <!--          <el-dialog draggable=true v-model="dialogFormVisible" title=":" center width="30%">-->
        <!--              <span>{{previewSrc}}</span>-->
        <!--              <template #footer>-->
        <!--                <span class="dialog-footer">-->
        <!--                  <el-button @click="dialogFormVisible = false">-->
        <!--                    -->
        <!--                  </el-button>-->
        <!--                  <el-button type="primary" @click="dialogFormVisible = false">-->
        <!--                    -->
        <!--                  </el-button>-->
        <!--                </span>-->
        <!--              </template>-->
        <!--          </el-dialog>-->
        <!--      </div>-->

        <el-button v-show="isScreenshot" class="close" @click="htmlToImage1">png</el-button>
        <el-button v-show="isScreenshot" class="save" @click="htmlToImage2">jpg</el-button>
        <div class="canvas-container">
            <div
                class="canvas"
                :style="{
          ...getCanvasStyle(canvasStyleData),
          width: changeStyleWithScale(canvasStyleData.width) + 'px',
          height: changeStyleWithScale(canvasStyleData.height) + 'px',
        }"
            >
                <ComponentWrapper
                    v-for="(item, index) in copyData"
                    :key="index"
                    :config="item"
                />
            </div>
        </div>
    </div>
</template>

<script>
import {getStyle, getCanvasStyle} from "@/utils/style";
import {mapState} from "vuex";
import ComponentWrapper from "./ComponentWrapper";
import {changeStyleWithScale} from "@/utils/translate";
import {toJpeg, toPng} from "html-to-image";
import {deepCopy} from "@/utils/utils";
import {getToken} from "@/utils/token";
import axios from "axios";
import getSaveData from '@/api/toSave'
import pubsub from 'pubsub-js'

export default {
    components: {ComponentWrapper},
    props: {
        isScreenshot: {
            type: Boolean,
            default: false,
        },
    },

    data() {
        return {
            // previewSrc : '',
            // formLabelWidth : '140px',
            // dialogFormVisible : false ,
            copyData: [],
        };
    },
    computed: mapState(["componentData", "canvasStyleData"]),
    created() {
        this.$set(this, "copyData", deepCopy(this.componentData));
        pubsub.subscribe('close', () => this.close());
    },
    methods: {
        getStyle,
        getCanvasStyle,
        changeStyleWithScale,

        close() {
            this.$emit("close");
        },

        async sendPreview1() {
            try {
                const dataUrl = await toPng(this.$refs.container.querySelector(".canvas"));

                const response = await fetch(dataUrl); //  URL
                const data = await response.blob();

                //  Blob 
                const blob = new Blob([data], {type: 'image/png'}); //  Data URL 

//  FormData  Blob 
                const formData = new FormData();
                formData.append('image', blob); // 'file' 

// 
                const config = {
                    headers: {
                        'Content-Type': 'multipart/form-data', //  Content-Type
                        Authorization: getToken()
                    },
                };

                const query = location.search.slice(1).split('&')
                const data2 = {}
                query.forEach(i => {
                    data2[`${i.split('=')[0]}`] = i.split('=')[1]
                })
                let designId = data2['design']
                let projId = data2['project']


                const url = `/api/v1/design/${designId}/generate-preview`// URL
                const result = await axios.post(url, formData, config);
                pubsub.publish('save')
                this.close()
            } catch (error) {
                console.error("Oops, something went wrong!", error);
            } finally {
                // this.close();
            }
        },


        htmlToImage1() {
            toPng(this.$refs.container.querySelector(".canvas"))
                .then((dataUrl) => {
                    const a = document.createElement("a");
                    a.setAttribute("download", "screenshot");

                    a.href = dataUrl;
                    console.log('dataUrl', dataUrl)
                    a.click();
                })
                .catch((error) => {
                    console.error("oops, something went wrong!", error);
                })
                .finally(this.close);
        },

        htmlToImage2() {
            toJpeg(this.$refs.container.querySelector(".canvas"))
                .then((dataUrl) => {
                    const a = document.createElement("a");
                    a.setAttribute("download", "screenshot");

                    a.href = dataUrl;
                    console.log('dataUrl', dataUrl)
                    a.click();
                })
                .catch((error) => {
                    console.error("oops, something went wrong!", error);
                })
                .finally(this.close);
        },
    },
};
</script>

